<template>
	<div class="list-warper" >
		<div class="login-header">
			<div @click="routerGo" class="back">
				<i></i>
				<span>返回</span>
			</div>
			
			<h1>{{this.$route.params.name}}</h1>
			
			<router-link to="/">
				<span class="login-home">
					<i></i>
				</span>
			</router-link>
		</div>
		
		
		
		<ul class="inner">
			<li>热门</li>
			<li>新书</li>
			<li>好评</li>
			<li>完结</li>
			<li>包月</li>
		</ul>
		<ul class="inner" v-show="sar">
			<li>全部</li>
			<li v-show="c" v-for="(c,index) in listDate.slice(0,4)" :key="index">
				{{c}}
			</li>
		</ul>
		
		
		
		
		<div class="inner-clear" v-for="(i,indexs) in Inner" :key="indexs">
			<router-link :to="/bookdeta/ + i._id">
			<div class="inner-cover">
				<img :src="[link + i.cover]" class="auto-img">
			</div>
			<div class="inner-right" >
				<h4>{{i.title}}</h4>
				<p>{{i.author}}<i>|</i>{{i.majorCate}}</p>
				<p>{{i.shortIntro}}</p>
				<p><span>{{i.latelyFollower}}万</span>人气
				<i>|</i>
				<span>{{i.retentionRatio}}%</span>读者留存
				</p>
			</div>
			</router-link>
		</div>
		
	</div>
</template>

<script>
	const url = "api/cats/lv2/"
	
	export default {
		name:"ListWarper",
		data(){
			return{
				sar:0,
				Inner:[],
				male:[],
				female:[],
				picture:[],
				link:'http://statics.zhuishushenqi.com',
			}
		},
		created() {
			this.axios.get(url).then(l => {
				let listUrl = l.data
				this.male = listUrl.male
				this.female = listUrl.female
				this.picture = listUrl.picture
			})
		},
		methods:{
			routerGo(){
				this.$router.go(-1);
			}
		},
		beforeRouteEnter(to,from,next){
			next(vm => {
				vm.axios.get(vm.innerList).then(i => {
					vm.Inner = i.data.books
					console.log(vm.Inner)
				})
			})
		},
		computed:{
			listDate(){
				let result = []
				this.male.map( m => {
					if(this.$route.params.name === m.major){
						result = m.mins
					}
				})
				this.female.map( m => {
					if(this.$route.params.name === m.major){
						result = m.mins
					}
				})
				this.picture.map( m => {
					if(this.$route.params.name === m.major){
						result = m.mins
					}
					
				})
				
				if(result.length !== 0){
					this.sar = true
				}else{
					this.sar = false
				}
				
				return result;
			},
			innerList(){
				let inner = `api/book/by-categories?gender=${this.$route.params.id}&type=hot&major=${this.$route.params.name}&minor=&start=0&limit=20`
				return inner
			}
		},
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	.list-warper{
		background-color: #fff;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
	}
		
	.login-header{
		width: 100%;
		height: 2.75rem;
		background-color: #b93321;
		color: #fff;
		display: block;
	
		.back{
			display: inline-block;
			text-align: center;
			height: 100%;
			width: 60px;
			float: left;
			
			i{
				display: inline-block;
				background: url(../../public/img/arror-back.png) no-repeat center;
				background-size: 125%;
				height: 100%;
				width: 0.9375rem;
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				font-size: 12px;
				margin-left: 2px;
				color:#fff;
			}
		}
		
		h1{
			width: 70%;
			display: inline-block;
			text-align: center;
			line-height: 2.75rem;
		}
		
		.login-home{
			background: url(../../public/img/home.png) no-repeat center;
			background-size: 35%;
			height: 100%;
			width: 14%;
			float: right;
			display: inline-block;
		}
		
	}

	.inner{
		width: 100%;
		height: 45px;
		border-bottom: 1px solid #ddd;
		
		li{
			float: left;
			height: 2.8125rem;
			line-height: 2.8125rem;
			padding: 0 10px;
			font-size: 13px;
			color: #666;
		}
		li:nth-child(1){
			color: #b93321;
		}
	}

	.inner-clear{
		width: 100%;
		height: 6.5625rem;
		padding: 0.9375rem;
		background-color: #F5F2ED;
		
		
		
		.inner-cover{
			float: left;
			width: 3.625rem;
			height: 100%;
			margin-right: 0.9375rem;
		}
		
		.inner-right{
			float: left;
			width: 75%;
			
			h4{
				font-size: 16px;
				font-weight: 700;
				color: #333;
			}
			p{
				-webkit-line-clamp: 1;
				white-space: nowrap;
				text-overflow: ellipsis;
				margin-top: 0.5rem;
				overflow: hidden;
				font-size: 12px;
				color: #999;
			}
			i{
				margin: 0 0.625rem;
			}
			span{
				color: #b93321;
			}
		}
	}
</style>
